import React, { PropTypes } from 'react';
import { Button, Form, Row, Input,Checkbox, Radio, Icon, Col, Select, Popconfirm} from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;
const CheckboxGroup = Checkbox.Group;
class FormLogin extends React.Component {

  state = {
    data: [] ,
    current: 0,
    value: 1,
    largeurbande: 1.4,
 };

  onSubmit = (e) => {
    e.preventDefault();
    const current = this.state.current + 1;
    this.setState({current});
  }

  onBack = (e) => {
    e.preventDefault();
    const current = this.state.current - 1;
    this.setState({current});
  }

   onChange = (e) => {
    this.setState({
      value: e.target.value,
    });
  }

  defaultParameter = (e) => {
    e.preventDefault();
    this.setState({
      value: 1,
    });
    this.props.form.setFieldsValue({uplink: "QPSK1/4",downlink: "QPSK1/4",});
  }

render() {
const options = [
  { label: 'okumura-hata', value: 'hata' },
  { label: 'cost 231', value: 'cost' },
  { label: 'k-facteurs', value: 'kfact' }];
const {getFieldDecorator} = this.props.form;
const {current, value, largeurbande, uplink, downlink} = this.state;
const formItemLayout = {
      labelCol: { span: 12 },
      wrapperCol: { span: 12 },
    };
const formButtonLayout = {
      wrapperCol: { span: 12, offset: 12 },
    };
    return (
      <Row justify="center" type="flex" style={{marginTop: 60 }} >
       
      {current===0 && <Col xs={{span: 20}} sm={{span: 16 }} md={{span: 12}} style={{background: "#e9e9e9", padding: 15 }}>
            <h1 className ="border"> CHOISSISSEZ UNE VILLE </h1>
            <Form layout="inline" style={{marginTop: 15}} > 
                <FormItem label="Choisissez la ville">
                  {getFieldDecorator('username', {initialValue: "yaounde"})(
                    <Select style={{ width: 140 }}>
                      <Option value="yaounde">Yaound</Option>
                      <Option value="douala">Douala</Option>
                      <Option value="maroua">Maroua</Option>
                    </Select>
                  )}
                </FormItem>
                <FormItem>
                 <Button key={current} onClick={this.onSubmit} type="primary">Next</Button> 
                </FormItem>
      
              </Form>
              </Col>}

        {current===1 && <Col span="20" style={{background: "#e9e9e9", padding: 15 }}>
            <h1 className ="border"> PARAMTRES GNRAUX </h1>
            <Form style={{marginTop: 15}} > 
                <FormItem {...formItemLayout} label="Mode de multiplexage">
                <RadioGroup onChange={this.onChange} value={this.state.value}>
                  <Radio value={1} >FDD</Radio>
                  <Radio value={2} >TDD</Radio>
                </RadioGroup>
                </FormItem>
                 {value===1 && <FormItem {...formItemLayout} label="Bande de frquence">
                  {getFieldDecorator('bandefreq', {initialValue: "first"})(                   
                    <Select >
                        <Option value="first">{"1900 =>1850 - 1910 / 1930 - 1990"}</Option>
                    </Select>
                  )}
                </FormItem>}
                {value === 2 && <FormItem {...formItemLayout} label="Bande de frquence">
                  {getFieldDecorator('bandefreq', {initialValue: "first"})(
                    <Select >
                        <Option value="first">{"umtsTDD1 =>1900 - 1920 "}</Option>
                    </Select>
                  )}
                </FormItem>}
                <FormItem {...formItemLayout} label="Largeur de bande(MHz) ">
                  {getFieldDecorator('largeurbande', {initialValue: largeurbande})(
                    
                    <Select style={{width: 75}}  >
                        <Option value={largeurbande} >{largeurbande}</Option>
                    </Select>
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="Modulation et de taux de codage en UPLINK ">
                  {getFieldDecorator('uplink', {initialValue: "QPSK1/4"})(
                    
                    <Select style={{width: 100}}  >
                        <Option value="QPSK1/4" >{"QPSK1/4"}</Option>
                        <Option value="QPSK1/8" >{"QPSK1/8"}</Option>
                    </Select>
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="Modulation et de taux de codage en DOWNLINK ">
                  {getFieldDecorator('downlink', {initialValue: "QPSK1/4"})(
                    
                    <Select style={{width: 100}}  >
                        <Option value="QPSK1/4" >{"QPSK1/4"}</Option>
                        <Option value="QPSK1/8" >{"QPSK1/8"}</Option>
                    </Select>
                    )}
                </FormItem>

                <FormItem {...formButtonLayout}>
                 <Button onClick={this.onBack} style={{marginRight: 10}} type="primary">Back</Button> 
                 <Button onClick={this.defaultParameter} style={{marginRight: 10}} type="primary">valeurs par dfaut</Button>
                 <Button key={current} onClick={this.onSubmit} type="primary">Next</Button> 
                </FormItem>
      
              </Form>
              </Col>}

            {current===2 && <Col span = "20" style={{background: "#e9e9e9", padding: 15 }}>
            
            <Form style={{marginTop: 15}} > 
            <h1 className ="border"> Title </h1>
                <FormItem {...formItemLayout} label="hauteur de l'enodeB (m)">
                  {getFieldDecorator('hauteurB', {initialValue: 50})(
                    <Input />
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="gain de l'enodeB(dBi)">
                  {getFieldDecorator('gainB', {initialValue: 9})(
                    <Input />
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="puissance d'mission max de l'enode B(dBm)">
                  {getFieldDecorator('puissanceMax', {initialValue: 46.0})(
                    <Input />
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="figure de bruit de l'e-node B (dB)">
                  {getFieldDecorator('bruitB', {initialValue: 2})(
                    <Input />
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="interfrence margin">
                  {getFieldDecorator('interMarginB', {initialValue: 2.4})(
                    <Input />
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="pertes dues au type d'antenne">
                  {getFieldDecorator('perteAntenne', {initialValue: 2})(
                    <Input />
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="Type d'antenne">
                  {getFieldDecorator('typANtenne', {initialValue: "omni"})(
                    <Select style={{ width: 140 }}>
                      <Option value="omni">Omnidirectionnelle</Option>
                    </Select>
                  )}
                </FormItem>
                <h1 className ="border"> PARAMTRES UE </h1>
                <FormItem {...formItemLayout} label="Hauteur de l'UE(dBi)">
                  {getFieldDecorator('hauteurUe', {initialValue: 1.5})(
                    <Input />
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="gain de l'UE (dBi)">
                  {getFieldDecorator('gainUe', {initialValue: 9})(
                    <Input />
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="puissance d'mission max de l'UE (dBm)">
                  {getFieldDecorator('puissUe', {initialValue: 5})(
                    <Input />
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="figure de bruit de l'UE">
                  {getFieldDecorator('bruitUe', {initialValue: 7})(
                    <Input />
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="interfrence marginde l'UE (dB)">
                  {getFieldDecorator('interMargin', {initialValue: 4})(
                    <Input />
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="pertes dues au corps humain">
                  {getFieldDecorator('corpsHumain', {initialValue: 0.01})(
                    <Input />
                    )}
                </FormItem>
                <h1 className ="border"> PARAMTRES ENVIRONNEMENTAUX </h1>
                <FormItem {...formItemLayout} label="planning margin (valeur entre 10 et 25)">
                  {getFieldDecorator('marplan', {initialValue: 15})(
                    <Input />
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="control channel overhead">
                  {getFieldDecorator('controlchanel', {initialValue: 20})(
                    <Input />
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="Most head amplifier gain">
                  {getFieldDecorator('ampliGain', {initialValue: 3})(
                    <Input />
                    )}
                </FormItem>
                <FormItem {...formItemLayout} label="Modles de propagation">
                  {getFieldDecorator('propaModel', {initialValue: ['hata', 'cost', 'kfact']})(
                       <CheckboxGroup options={options} />
                    )}
                </FormItem>
                <FormItem {...formButtonLayout}>
                 <Button onClick={this.onBack} style={{marginRight: 10}} type="primary">Back</Button> 
                 <Button key={current} onClick={this.onSubmit} type="primary">Next</Button> 
                </FormItem>
      
              </Form>
              </Col>}
      </Row>    
    );
  }
}

const PlanForm = Form.create()(FormLogin);
export default PlanForm;


