import React, { PropTypes } from 'react';
import { Button, Form, Row, Input, Icon, Col, Popconfirm} from 'antd';
const FormItem = Form.Item;

class FormLogin extends React.Component {

  onSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.login();
      }
    });
  }

  render() {
const {getFieldDecorator} = this.props.form;
    return (
      <Row justify="center" type="flex" style={{marginTop: 200}} >
      
      <Col xs={{span: 16}} sm={{span: 12}} md={{span: 8}} style={{background: "#e9e9e9", padding: 15 }}>
      <Col span="15" offset="9" >
      <img width="100" height="60" src="https://camerpro.com/wp-content/uploads/2016/07/logo-nexttel.jpg"/>
      </Col>
      <Form layout="vertical" style={{marginTop: 80}} >
          <FormItem label="Nom d'utilissateur">
            {getFieldDecorator('username', {
              rules: [{ required: true, message: "Entrez votre nom d'utilissateur"  }],
            })(
              <Input placeholder="Taper un nom" style={{fontSize: 14}} />
            )}
          </FormItem>
          <FormItem label="Mot de Passe">
            {getFieldDecorator('password',{
              rules: [{ required: true, message: 'Entrez votre mot de passe' }],
            })(<Input placeholder="Taper un mot de passe" style={{fontSize: 14}} />)}
          </FormItem>
          <FormItem>
           <Button style={{width: "100%"}} onClick={this.onSubmit} type="primary">Submit<Icon type="login" /></Button> 
          </FormItem>

        </Form>
        </Col>
      </Row>
      
    );
  }
}

const Login = Form.create()(FormLogin);
export default Login;


